import React, { Component } from 'react';
import './App.css';
import CircleSelector from './components/CircleSelector';
import Circles from './components/Circles'



class App extends Component {
  constructor() {
    super();
    this.state = { 
      selNum: 1
    }
  }
   circArr = [ 1, 2, 3, 4]

  handleSelectCircle = (idx ) => (
      this.setState({ selNum: idx })
    ) 


    


  render() {
    return (
      <div className="App">
        <header className="App-header">UNIT 4 FINAL ASSESSMENT</header>
        <main>
        <CircleSelector selNum={this.state.selNum}
          circleNumbers={this.circArr}
          handleSelectCircle={this.handleSelectCircle}
        />
        <Circles  
        selNum={this.state.selNum}
        circleNumbers={this.circArr}
        />
        </main>
      </div>
    );
  }
}

export default App;