import React from 'react'
import './Circles.css';


const Circles = (props) => (

    <div className='Circles'>
        {props.circleNumbers.map((cir, idx) => 
        <div 
        key={idx + 1} 
        className={(props.selNum === idx + 1) ? "selected" : ""} 
        
        
        ></div>
    
        )}
    </div> 
    );

export default Circles;