import React from 'react';
import styles from './CircleSelector.module.css';

function CircleSelector(props) {
    return(
    
    <div className={styles.CircleSelector}>
    {props.circleNumbers.map((c, idx) =>
        
        <button className={(props.selNum === idx + 1) ? styles.selected : ""}
        key={idx + 1}
    onClick={() => props.handleSelectCircle(idx + 1)} >{props.selNum === idx + 1 ? `${idx + 1} selected`: `${idx + 1} selected`}</button> 
    
    
    
    )}
    </div>
    );
}
  


export default CircleSelector;


